import { Injectable, EventEmitter, Output } from '@angular/core';
import { Response } from '@angular/http';
import { HttpClient } from '@angular/common/http';

import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/throw';

@Injectable()
export class LoginService {
  @Output() setEmail: EventEmitter<any> = new EventEmitter();
  api: string = 'http://www.blackport.cf/api/index.php/member/';
  func: string = '';
  res: any;

  constructor(private http: HttpClient) { }

  login(data): Observable<any> {
    this.func = 'login';
    return this.http.post(this.api + this.func, data).map((res: Response) => {
      this.res = res;
      if (this.res.status === '1') {
        localStorage.setItem('token', '1');
        localStorage.setItem('profile', JSON.stringify(this.res));
        console.log('set profile: ', JSON.stringify(this.res));
        console.log('set token: 1');
        this.setEmail.emit(this.res.res.email);
      }
      return this.res;
    });
  }

  private handleError(error: any) {
    return Observable.throw(error.json().message) || 'service error';
  }

  logout() {
    localStorage.removeItem('token');
    localStorage.removeItem('profile');
  }
}



