import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';

import { Res } from '../res';
import { Member } from '../member/member';
import { LoginService } from '../services/login.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  res: Res;

  myForm: FormGroup;
  email: FormControl;
  password: FormControl;

  errorMessage: string;
  is_loading: boolean = false;

  constructor(private fb: FormBuilder, private loginService: LoginService, private router: Router) {
    this.email = fb.control('', Validators.compose([Validators.required, LoginComponent.emailValidator]));
    this.password = fb.control('', Validators.required);
    this.myForm = fb.group({
      'email': this.email,
      'password': this.password
    });
  }

  static emailValidator(control: FormControl) {
    let emailregxp =  /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return emailregxp.test(control.value) ? null : { invalidEmail: true };
  }

  ngOnInit() {
  }

  form() {
    console.log(this.myForm.value);
    this.is_loading = true;
    this.loginService.login(this.myForm.value).subscribe((res) => {
      this.res = res;
      this.is_loading = false;
      window.alert(this.res.description);
      console.log(this.res);
      if (this.res.redirect !== false) {
        this.router.navigate([this.res.redirect]);
      }
    }, (error) => {
      this.errorMessage = <any> error;
      this.is_loading = false;
    });
  }

}



