import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { RouterModule } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { HttpClientModule } from '@angular/common/http';

import { AppComponent } from './app.component';
import { MemberComponent } from './member/member/member.component';

import { MemberService } from './services/member.service';
import { LoginService } from './services/login.service';
import { AuthGuardService } from './services/auth-guard.service';
import { HeaderComponent } from './-include/header/header.component';
import { SidebarComponent } from './-include/sidebar/sidebar.component';
import { MemberDetailComponent } from './member/member-detail/member-detail.component';
import { MemberFormComponent } from './member/member-form/member-form.component';
import { LoginComponent } from './login/login.component';

@NgModule({
  declarations: [
    AppComponent,
    MemberComponent,
    HeaderComponent,
    SidebarComponent,
    MemberDetailComponent,
    MemberFormComponent,
    LoginComponent
  ],
  imports: [
    BrowserModule,
    // HttpModule,
    HttpClientModule,
    FormsModule,
    ReactiveFormsModule,
    RouterModule.forRoot([
      { path: '', component: MemberComponent },
      { path: 'member/view/:id', component: MemberDetailComponent },
      { path: 'member/edit/:id', component: MemberFormComponent, canActivate: [AuthGuardService] },
      { path: 'member/create', component: MemberFormComponent, canActivate: [AuthGuardService] },
      { path: 'login', component: LoginComponent },
      { path: '**', redirectTo: '' },
    ], { useHash: true })
  ],
  providers: [MemberService, LoginService, AuthGuardService],
  bootstrap: [AppComponent]
})
export class AppModule { }



