import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { Location } from '@angular/common';

import { Member } from '../member';
import { MemberService } from '../../services/member.service';

import { Subscription } from 'rxjs/Subscription';

import { Title } from '@angular/platform-browser';
@Component({
  selector: 'app-member-detail',
  templateUrl: './member-detail.component.html',
  styleUrls: ['./member-detail.component.css']
})
export class MemberDetailComponent implements OnInit, OnDestroy {
  sub: Subscription;
  id: number;
  code: string;
  member: Member;

  errorMessage: string;
  is_loading: boolean = false;

  constructor(private route: ActivatedRoute, private memberService: MemberService, private location: Location, private title: Title) { }

  ngOnInit() {
    this.sub = this.route.params.subscribe((params: Params) => {
      this.id = params['id'];
      this.get_member_by_id(this.id);
    });
  }

  ngOnDestroy() {
    // console.log('OnDestroy')
    this.sub.unsubscribe();
  }

  get_member_by_id(id) {
    this.is_loading = true;
    this.memberService.get_members_by_id(id).subscribe((member) => {
      this.member = member;
      this.title.setTitle(`${this.member.firstname} ${this.member.lastname}`);
      console.log(this.member);
      this.is_loading = false;
    }, (error) => {
      this.errorMessage = <any> error;
      this.is_loading = false;
    });
  }

  goBack() {
    this.location.back();
  }

}



