import { Injectable } from '@angular/core';

import { Http, Response } from '@angular/http';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { Observable } from 'rxjs/Observable';
// import 'rxjs/add/operator/map';
// import 'rxjs/add/operator/catch';
// import 'rxjs/add/observable/throw';

import { Member } from '../member/member';
import { Res } from '../res';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

@Injectable()
export class MemberService {
  api = 'http://www.blackport.cf/api/index.php/member/';
  func = '';
  data: Res;
  res: Res;
  constructor(private http: HttpClient) { }

  get_members(): Observable<Member[]> {
    this.func = 'get_member';
    return this.http.get<Member[]>(this.api + this.func);
  }

  get_members_by_id(id): Observable<Member> {
    this.func = 'get_member_by_id/' + id;
    return this.http.get<Member>(this.api + this.func);
  }

  insert_member(data): Observable<Res> {
    this.func = 'insert_member';
    return this.http.post<Res>(this.api + this.func, data, httpOptions);
  }

  update_member(data): Observable<Res> {
    this.func = 'update_member';
    return this.http.post<Res>(this.api + this.func, data, httpOptions);
  }
}



