import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { Member } from '../member';
import { MemberService } from '../../services/member.service';

import * as swal from 'sweetalert';

@Component({
  selector: 'app-member',
  templateUrl: './member.component.html',
  styleUrls: ['./member.component.css']
})
export class MemberComponent implements OnInit {
  name: string;
  img: string;
  toggle: boolean;

  member: Member[];
  members: Member[];

  is_loading: boolean = false;
  errorMessage: string;

  constructor(private router: Router, private memberService: MemberService) {
    this.name = 'Black Adisorn';
    this.toggle = false;
    this.img = 'http://via.placeholder.com/350x150';
  }

  ngOnInit() {
    this.member = [
      new Member('1', 'แดง', 'มาหาหน่อย', '22', '123', 'f'),
      new Member('2', 'เขียว', 'กำลังไปหา', '18', '345', 'm')
    ];

   this.get_members();
  }

  swalert() {
    swal('Here\'s a message!');
  }

  get_members() {
    this.memberService.get_members().subscribe((members) => {
      this.members = members;
    }, (error) => {
      this.errorMessage = <any> error;
      this.is_loading = false;
    });
  }

  show_member_detail(member: Member): void {
    this.router.navigate(['/member/view', member.id]);
  }

  onSearch(search: String) {
    if (search !== '') {
      this.members = this.members.filter(item => item.firstname.toLowerCase().includes(search));
    }else {
      this.get_members();
    }
  }

  alert() {
    this.name = 'Dejavu.';
    alert('Hey Alert');
  }

}



