import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';

import { Location } from '@angular/common';

import { Member } from '../member';
import { Res } from '../../res';
import { MemberService } from '../../services/member.service';

import { Subscription } from 'rxjs/Subscription';

import { Title } from '@angular/platform-browser';

@Component({
  selector: 'app-member-form',
  templateUrl: './member-form.component.html',
  styleUrls: ['./member-form.component.css']
})
export class MemberFormComponent implements OnInit {
  sub: Subscription;
  id: number;
  code: string;
  member: Member;
  form_title: string;

  errorMessage: string;
  is_loading: boolean = false;
  res: Res;

  constructor(
    private route: ActivatedRoute,
    private memberService: MemberService,
    private location: Location,
    private title: Title,
    private router: Router
  ) {
    this.member = new Member('', '', '', '', '', '');
  }

  ngOnInit() {
    this.sub = this.route.params.subscribe((params: Params) => {
      console.log(params['id']);
      if (params['id'] === undefined) {
        // this.member = new Member('', '', '', '', '', '');
        this.form_title = 'เพิ่มสมาชิก';
      }else {
        this.form_title = 'แก้ไข';
        this.id = params['id'];
        this.get_member_by_id(this.id);
      }
    });
  }

  get_member_by_id(id) {
    this.is_loading = true;
    this.memberService.get_members_by_id(id).subscribe((member) => {
      this.member = member;
      this.title.setTitle(`${this.member.firstname} ${this.member.lastname}`);
      this.is_loading = false;
    }, (error) => {
      this.errorMessage = <any> error;
      this.is_loading = false;
    });
  }

  form(value: any) {
    if (value.id === '') {
      this.memberService.insert_member(value).subscribe((res) => {
        this.res = res;
        this.is_loading = false;
        this.router.navigate([this.res.redirect]);
      }, (error) => {
        this.errorMessage = <any> error;
        this.is_loading = false;
      });
    }else {
      this.memberService.update_member(value).subscribe((res) => {
        this.res = res;
        this.is_loading = false;
        if (!this.res.redirect) {
          this.router.navigate([this.res.redirect]);
        }
      }, (error) => {
        this.errorMessage = <any> error;
        this.is_loading = false;
      });
    }
  }

}



